import tools from '../tools/index.vue';
import user from '../user/index.vue';
import element from '../element/index.vue';
import work from '../work/work.vue'
export default {
	components: {
		tools,
		user,
		element,
		work
	},
	data() {
		return {
			PageCur: 'mrxc'
		};
	},
	methods: {
		NavChange: function(e) {
			this.PageCur = e.currentTarget.dataset.cur;
		}
	}
};
