import uniSection from '@/components/uni-section/uni-section.vue'
import uniGrid from '@/components/uni-grid/uni-grid.vue'
import uniGridItem from '@/components/uni-grid-item/uni-grid-item.vue'
import uniBadge from '@/components/uni-badge/uni-badge.vue'
import uniList from "@/components/uni-list/uni-list.vue"
import uniListItem from "@/components/uni-list-item/uni-list-item.vue"
import serverurl from "@/common/globalconfigs.js"

export default {
	components: {
		uniSection,
		uniGrid,
		uniGridItem,
		uniBadge,
		uniList,
		uniListItem
	},
	data() {
		return {
			worklist: [],
			levellist: [],
			hotlist: []
		}
	},
	created() {
		this.getWorkList()
		this.getLevelList()
		this.getHotList()
	},
	methods: {
		// 办事部门
		getWorkList() {
			uni.request({
				url: serverurl + '/gxfrTL/get_base_dic',
				success: (res) => {
					// console.log(res.data)
					let wlist = res.data.Data.D001
					this.worklist = wlist
					// console.log(this.worklist)
				}
			})
		},
		workchange(index){
			// console.log(index)
			uni.navigateTo({
			    url: '../shared/department-list?id=' + index
			});
		},
		//事项层级列表
		getLevelList() {
			uni.request({
				url: serverurl + '/gxfrTL/get_affair_level_type',
				data: {
					userType: 2
				},
				success: (res) => {
					// console.log(res.data)
					let wlist = res.data.Data
					this.levellist = wlist
					// console.log(this.levellist)
				}
			})
		},
		levelchange(name, level){
			console.log('name', name)
			console.log('level', level)
			uni.navigateTo({
			    url: '../shared/department-list?name=' + name + '&level=' + level
			});
		},
		//热门办理事项
		getHotList() {
			uni.request({
				url: serverurl + '/gxfrTL/get_hot_affairs',
				data: {
					userType: 2
				},
				success: (res) => {
					// console.log(res.data)
					let wlist = res.data.Data
					this.hotlist = wlist
					// console.log(this.hotlist)
				}
			})
		},
		hotchange(index){
			console.log(index)
		}
	}
}


